      .col-lg-8(data-aos="fade-left")
        p Aunque la importancia de la educacin para la promocin de la salud y la prevencin de la enfermedad se reconoce cada vez ms, lo cierto es que usualmente esta se ha basado en la transmisin de ideas y formacin de hbitos saludables de acuerdo con criterios medicalizados, de forma prescriptiva, sin tener en cuenta los propios saberes y prcticas que sobre su salud tienen los individuos, familias y colectivos, y sin contar con las posibilidades reales que brinda el contexto para ser y actuar saludablemente.
        .BG02.mb-4(data-aos="flip-up")
          .row.justify-content-center.align-items-center
            .col-lg-1.col-2.pe-2.py-1.mb-lg-0.mb-4
              img(src='@/assets/curso/temas/img05.svg')
            .col.p-4 
              p.mb-0 Es necesario construir la educacin para la salud como un proceso pedaggico, que aclare los siguientes interrogantes:

    .BGM07.px-md-5.px-4.mb-5
      .row.justify-content-center
