            img(src='@/assets/curso/tema5/img03.svg' alt="Muestra tres interrogantes: 01 Cual es el tipo de humanos que se pretende formar?. 02 Qu se quiere ensear?. 03 Para qu y cmo hacerlo?")
    
    p.mb-5 Esta falta de claridad pedaggica hace que se confunda la Educacin para la Salud (EpS) con la informacin o el #[em marketing] en salud, lo que se ve reflejado, por ejemplo:

    .BG05.p-4.mb-5(data-aos="flip-up")
      .row.justify-content-center.align-items-center
        .col-3.col-sm-2.col-lg-1
          img(src="@/assets/curso/tema5/img04.svg")
        .col
          .row.justify-content-between.align-items-center
            .col.mb-3.mb-sm-0
              h4.mb-1 #[em Word no tobacco day]
              .h5.mb-0 En la inversin de grandes cantidades de dinero en el desarrollo de campaas publicitarias donde se transmiten ciertas valoraciones, mediante las cuales se esperara que la gente cambiara su forma de actuar, pero que pocas veces o nunca se evalan.
            .col-sm-auto
              a.boton.color-acento-contenido(href="https://as1.ftcdn.net/v2/jpg/04/24/13/50/1000_F_424135018_aCrGybHPpQGCHZ9pHcuxYzXe1zQ8WMNx.jpg" target="_blank")
                span Ir al sitio
                i.fas.fa-link
              
    .row.justify-content-center.align-items-end.mb-5
      .col-lg-7.order-lg-1.order-2(data-aos="fade-right")
        p En este sentido, es menester decir que la prctica educativa no es proteccionista, ni unidireccional, ni tampoco pretende controlar a los sujetos, sino servir de mediacin con los sujetos y las comunidades para lograr su accin protagnica en la construccin de su salud. Adicionalmente, hay debilidades en la formacin del talento humano en salud para adelantar esta clase de procesos: se desconoce el avance que a lo largo de la historia ha tenido la EpS, se desconoce el acervo terico que existe en las ciencias de la educacin y cmo podra aplicarse.
        p.mb-0 Todo lo anterior, lleva a la imperiosa necesidad de que los trabajadores de la salud hagan conciencia sobre el propio modelo pedaggico con el que actan y perpetan ciertas prcticas, basadas, por una parte, en el supuesto poder superior del conocimiento cientfico y, por otra, en ideas homogeneizantes sobre la salud que no consideran los determinantes sociales (y ambientales) de la salud como aspectos trascendentales que condicionan y afectan las posibilidades de que los sujetos construyan una vida saludable, digna de ser vivida.
      .col-lg-5.col-10.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/tema5/img05.png' alt="Imagen decorativa")

    .BG02.mb-4(data-aos="flip-up")
      .row.justify-content-center.align-items-center
        .col-lg-1.col-2.pe-2.py-1.mb-lg-0.mb-4
          img(src='@/assets/curso/temas/img05.svg')
        .col.p-4 
          p.mb-0 Para ello, se ha diseado el curso de #[strong Educacin para la Salud en el marco de las RIAS],  en el cual encontrar las orientaciones pedaggicas, metodolgicas y didcticas que servirn de herramienta para los equipos tcnicos de salud (tanto del Plan de Beneficios de Salud con cargo a la UPC como los que sean contratados mediante el Plan de Intervenciones Colectivas o las Direcciones Territoriales de Salud, que adelantan las respectivas acciones de Gestin de la Salud Pblica), de modo que pueda tener las claridades suficientes que le permitan desarrollar los procesos pedaggicos para la salud.

    separador
    #t_5_1.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 5.1 Aproximaciones generales a la Educacin para la Salud

    .row.justify-content-center.mb-5
      .col-lg-5.col-10.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema5/img06.png' alt="Imagen decorativa")
      .col-lg-7(data-aos="fade-left")
        p.mb-0 La educacin para la salud ha tenido diversas intencionalidades de acuerdo con las caractersticas del desarrollo histrico de la humanidad. En nuestro pas, por ejemplo, a principios del siglo XX, cuando la preocupacin estaba centrada en las enfermedades epidmicas y endmicas, el problema de morbimortalidad se convirti en la mayor preocupacin de los gobiernos, la EpS se centr en el tema de la higiene, la educacin sanitaria, la profilaxis, el disciplinamiento de los cuerpos (como restauracin fisiolgica), de la mano de la medicalizacin de la sociedad y la escolarizacin de los saberes sanitarios.

    .row.justify-content-center.mb-5
      .col-lg-6.col-10.mb-lg-0.mb-4(data-aos="fade-right")
        .BG10.hg-03
          .figure
            img(src='@/assets/curso/tema5/img07.jpg' alt="Imagen decorativa")
          .px-4.pb-4
            .row.justify-content-around.align-items-center
              .col
                .h4.mb-0 Siglo XX
              .col-lg-2.col-3.img-01
                img(src='@/assets/curso/tema5/img09.svg' alt="Imagen decorativa")
            p.mb-0 Tom fuerza el concepto de salud pblica, orientada hacia el perfeccionamiento de la salud individual y colectiva, las acciones educativas se orientaron a los espacios donde hubiera mayor concentracin de personas (escuelas, lugares de trabajo, comunidades barriales, etc.), para ensearle a la gente cmo manejar problemas de desnutricin, enfermedades gastrointestinales y enfermedades inmunoprevenibles, entre otros aspectos.
      .col-lg-6.col-10(data-aos="fade-left")
        .BG11.hg-03
          .figure
            img(src='@/assets/curso/tema5/img08.jpg' alt="Imagen decorativa")
          .px-4.pb-4
            .row.justify-content-around.align-items-center
              .col
                .h4.mb-0 Dcada de los 60
              .col-lg-2.col-3.img-01
                img(src='@/assets/curso/tema5/img09.svg' alt="Imagen decorativa")
            p.mb-0 Con la comprensin de los determinantes sociales y ambientales de la salud, la estrategia educativa se centr en lograr que la gente tuviera el mayor control posible sobre los aspectos que condicionan su salud, mediante la generacin de responsabilidad sobre su estilo de vida (autocuidado-autonoma).

    .BG02.mb-5(data-aos="flip-up")
      .row.justify-content-center.align-items-center
        .col-lg-1.col-2.pe-2.py-1.mb-lg-0.mb-4
          img(src='@/assets/curso/temas/img05.svg')
        .col.p-4 
          p.mb-0 En este contexto, la EpS se centra en la promocin de ciertos comportamientos y el desarrollo de habilidades y capacidades de los individuos para gestionar su salud, hasta naturalizar la idea del querer ser saludable, reduciendo as los riesgos para la salud; lo cual, a su vez, tendra una incidencia en las condiciones sociales, econmicas y ambientales del territorio.

    .h5.text-center.mb-5 Las anteriores posturas se inscriben en dos perspectivas epistemolgicas con relacin a la educacin para la salud que se presentan en la siguiente infografa:

    .row.justify-content-center
      .col-auto.BGR12.px-5.py-2(data-aos="fade-right")
