      .col-lg-5.mb-lg-0.mb-4
        .BGR10S.px-5.py-3.mb-5(data-aos="fade-right")
          .h4.text-center.mb-0 Pensamiento positivista (clsico, moderno, eurocntrico y patriarcal)
        .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
          .col-2.pe-0.BG10.hg-04.p-2
            .p.text-center.text-bold.mb-0(style="font-size: 32px") 01
          .col.ps-0
            .BGR13S.hg-04.py-2.ps-2.pe-4
              p.mb-0 El sujeto es considerado como el centro de la racionalidad.
        .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
          .col-2.pe-0.BG10.hg-04.p-2
            .p.text-center.text-bold.mb-0(style="font-size: 32px") 02
          .col.ps-0
            .BGR13S.hg-04.py-2.ps-2.pe-4
