        .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
          .col-2.pe-0.BG10.hg-04.p-2
            .p.text-center.text-bold.mb-0(style="font-size: 32px") 03
          .col.ps-0
            .BGR13S.hg-04.py-2.ps-2.pe-4
              p.mb-0 Productor del conocimiento cientfico universal que orientaba el saber disciplinar como una verdad absoluta.
        .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
          .col-2.pe-0.BG10.hg-04.p-2
            .p.text-center.text-bold.mb-0(style="font-size: 32px") 04
          .col.ps-0
            .BGR13S.hg-04.py-2.ps-2.pe-4
              p.mb-0 Construccin de saberes hegemnicos, que determinaron en un momento especfico la delimitacin de criterios de verdad absolutos.      
        .BG02.hg-05(data-aos="fade-right")
          .row.justify-content-center.align-items-center
