              img(src='@/assets/curso/temas/img05.svg')
            .col.p-4
              p.mb-0 Confiriendo poder sobre los dems a quienes ostentaran dichos discursos y normalizando aquellos comportamientos deseables que se van instituyendo como normales y homogeneizantes.

      .col-lg-5
        .BGR11S.px-5.py-3.mb-5(data-aos="fade-left")
          .h4.text-center.mb-0 Pensamiento <br>posestructuralista
        .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
          .col-2.pe-0.BG11.hg-04.p-2
            .p.text-center.text-bold.mb-0(style="font-size: 32px") 01
          .col.ps-0
            .BGR13S.hg-04.py-2.ps-2.pe-4
              p.mb-0 El sujeto es producido por las constantes relaciones de poder que se tejen en las interacciones cotidianas.
        .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
          .col-2.pe-0.BG11.hg-04.p-2
            .p.text-center.text-bold.mb-0(style="font-size: 32px") 02
          .col.ps-0
            .BGR13S.hg-04.py-2.ps-2.pe-4
              p.mb-0 Se convierte en un sujeto producido, fundado, instituido por los otros y por el contexto.
        .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
          .col-2.pe-0.BG11.hg-04.p-2
            .p.text-center.text-bold.mb-0(style="font-size: 32px") 03
          .col.ps-0
            .BGR13S.hg-04.py-2.ps-2.pe-4
              p.mb-0 El saber se construye permanentemente y no hay verdades absolutas.
        .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
          .col-2.pe-0.BG11.hg-04.p-2
            .p.text-center.text-bold.mb-0(style="font-size: 32px") 04
          .col.ps-0
            .BGR13S.hg-04.py-2.ps-2.pe-4
              p.mb-0 El mundo, la cultura y el conocimiento se construyen mutuamente en la interaccin de los sujetos con diversas formas de ser y de hacer.
        .BG02.hg-05(data-aos="fade-left")
          .row.justify-content-center.align-items-center
            .col-lg-2.col-3.mb-lg-0.mb-4
              img(src='@/assets/curso/temas/img06.svg')
            .col.p-4
              p.mb-0 Reconociendo las ideas y culturas no hegemnicas, como otros modos de comprender y construir el mundo con relaciones de poder ms horizontales. Relevan el ejercicio del poder individual y de la resistencia en la vida cotidiana.
    
    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="flip-up")
        .tarjeta-avatar-b(data-aos="fade-right")
          .tarjeta-avatar-b__img(style="min-width: 9%")
            img(src='@/assets/curso/temas/img07.svg' alt='Alerta')
          .tarjeta.BGR04N(style="margin-left:-5%")
            .p-4
              .h4.mb-0 Precisamente, estas posturas pedaggicas definen unos alcances y transformaciones de la Educacin para la Salud, en trminos pedaggicos y didcticos.

    separador
    #t_5_2.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 5.2 Transformaciones del concepto Educacin para la Salud

    .BGIMG01.mb-5(data-aos="flip-up")
      .row.justify-content-start.align-items-center
          .col-lg-7.order-lg-1.order-2
            .row.justify-content-start.align-items-center
              .col-2.ps-5.pe-4.py-4
                img(src='@/assets/curso/temas/img01.svg')
              .col
                p.mb-0 El concepto, la acepcin y la aplicacin de la Educacin para la Salud han tenido transformaciones en su alcance y objetivos que definen sus cmo, a lo largo del siglo XX, basadas en las posturas epistemolgicas explicadas en el apartado anterior.
          .col-5.d-lg-none.d-flex
          .col-lg-3.col-6.px-lg-2.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema5/img11.png' alt="Imagen decorativa")

    .row.justify-content-start.align-items-center.mb-4(data-aos="fade-right")
      .col-auto.px-0
        img(src='@/assets/curso/temas/img04.svg' style="max-width: 40px;")
      .col-auto.BGRS08
        .h4.mb-0 Educacin para la Salud como sinnimo de Informacin en Salud
    
    p.mb-5 La Educacin para la Salud (EpS) es vista desde un punto de vista informativo prescriptivo, est ligada al nacimiento de la comunicacin para el desarrollo, la cual consista:
    .h4.text-center.mb-5 XX: nace al final de la Segunda Guerra Mundial

    .row.justify-content-center.mb-4
      .col-lg-4.col-md-8.BG03.p-4.mb-lg-0.mb-4(style="border-radius: 10px 0 0 10px; border-right: 2px solid #DDDDDD; box-shadow: 2px 2px 10px #DEDEDE" data-aos="fade-right")
        .h3.mb-2(style="color: #D7DADD") 01
        .row.justify-content-center.mb-4
          .col-lg-4.col-6
            img(src='@/assets/curso/tema5/img12.svg' alt="Imagen decorativa")
        p.text-center.mb-0 El concepto inicia con el paradigma desarrollista econmico en los 40, 50 y 60, donde el desarrollo se ve como el crecimiento de la economa, de la produccin y del comercio.
      .col-lg-4.col-md-8.BG03.p-4.mb-lg-0.mb-4(style="box-shadow: 2px 2px 10px #DEDEDE" data-aos="flip-up")
        .h3.mb-2(style="color: #D7DADD") 02
        .row.justify-content-center.mb-4
          .col-lg-4.col-6
            img(src='@/assets/curso/tema5/img13.svg' alt="Imagen decorativa")
        p.text-center.mb-0 La informacin se orienta a eliminar la brecha entre pases pobres y ricos, copiando el modelo de EE. UU. y Europa, desde una visin asistencialista.
      .col-lg-4.col-md-8.BG03.p-4(style="border-radius: 0 10px 10px 0; border-left: 2px solid #DDDDDD; box-shadow: 2px 2px 10px #DEDEDE" data-aos="fade-left")
        .h3.mb-2(style="color: #D7DADD") 03
        .row.justify-content-center.mb-4
          .col-lg-4.col-6
            img(src='@/assets/curso/tema5/img14.svg' alt="Imagen decorativa")
        p.text-center.mb-0 En este paradigma, el personal de salud es el detentor del conocimiento, y los otros (llmese paciente o comunidad) son receptivos pasivos de la informacin; la EpS es sinnimo de la informacin en salud, la cual se suministra como una pldora o receta que los individuos deben ingerir siguiendo las instrucciones del personal de salud. 

    .row.justify-content-start.align-items-center.mb-4(data-aos="fade-right")
      .col-auto.px-0
        img(src='@/assets/curso/temas/img04.svg' style="max-width: 40px;")
      .col-auto.BGRS08
        .h4.mb-0 Educacin para la Salud con el objetivo de impactar conductas
    
    .row.justify-content-center.mb-5
      .col-lg-5.col-10.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema5/img15.png' alt="Imagen decorativa")
      .col-lg-7(data-aos="fade-left")
        p.mb-0 Ya en los 70, se cambia el paradigma, la informacin se orienta al cambio de conducta, con un enfoque ms educativo; se da inicio a lo que conocemos como Educacin para la Salud. La Educacin para la Salud es dirigida a impactar la conducta, y tiene como fin generar cambios en los estilos de vida a partir de la implementacin de comportamientos saludables.  Para lograr esto, se articulan varios enfoques: el de creencias en salud, que parte de que son las creencias las que influyen en los comportamientos de las personas y en su aceptacin de las medidas de salud recomendadas; y la comunicacin persuasiva, basada en la CAP (conocimientos, actitudes y prcticas), donde las actitudes determinarn los comportamientos saludables.
    
    .BG05.p-4.mb-5
      .row.justify-content-center.align-items-center
        .col-lg-2.col-4.px-4.mb-lg-0.mb-4(data-aos="fade-right")
          img(src='@/assets/curso/tema5/img16.svg' alt="Imagen decorativa")
        .col-lg-10(data-aos="fade-left")
          .p.mb-0 La #[strong comunicacin persuasiva], llamada tambin difusionista, considera que el aportar conocimiento, por ejemplo, en temas de salud, har que las personas cambien de actitud, y eso har que cambien su comportamiento; el medio por excelencia de este enfoque son los medios de comunicacin masiva.

    .BG02.mb-5(data-aos="flip-up")
      .row.justify-content-center.align-items-center
        .col-lg-1.col-2.pe-2.py-3.mb-lg-0.mb-4
          img(src='@/assets/curso/temas/img05.svg')
        .col.p-4 
          p.mb-0 Este enfoque da nacimiento al mercadeo social, la educacin sanitaria, la promocin de la salud y el edu-entretenimiento.  Con el mercadeo social, asociado al paradigma difusionista de la informacin, se pretende promover prcticas y productos de beneficio social desde los medios de comunicacin, orientados al cambio de comportamiento individual; por ejemplo, evitar el consumo de tabaco o de grasa.  Los objetivos de este enfoque son:
    
    .row.justify-content-center.mb-5
      .col-lg-6
        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Figura 6
          span Objetivos enfoque cambio de conducta
        .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
          .col-2.pe-0.BG11.hg-06.p-2
            .p.text-center.text-bold.mb-0(style="font-size: 32px") 01
          .col.ps-0
            .BGR13S.hg-06.py-2.ps-2.pe-4
              p.mb-0 Persuadir o disuadir a las personas en relacin con la realizacin de determinada conducta.
        .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
          .col-2.pe-0.BG11.hg-06.p-2
            .p.text-center.text-bold.mb-0(style="font-size: 32px") 02
          .col.ps-0
            .BGR13S.hg-06.py-2.ps-2.pe-4
              p.mb-0 Anticiparse a que los sujetos adopten comportamientos que afecten su salud.
        .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
          .col-2.pe-0.BG11.hg-06.p-2
            .p.text-center.text-bold.mb-0(style="font-size: 32px") 03
          .col.ps-0
            .BGR13S.hg-06.py-2.ps-2.pe-4
              p.mb-0 Promocionar los derechos y las acciones de cuidado y autocuidado.
        .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
          .col-2.pe-0.BG11.hg-06.p-2
            .p.text-center.text-bold.mb-0(style="font-size: 32px") 04
          .col.ps-0
            .BGR13S.hg-06.py-2.ps-2.pe-4
              p.mb-0 Reforzar comportamientos y saberes favorables para promover y mantener la salud.
        .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
          .col-2.pe-0.BG11.hg-06.p-2
            .p.text-center.text-bold.mb-0(style="font-size: 32px") 05
          .col.ps-0
            .BGR13S.hg-06.py-2.ps-2.pe-4
              p.mb-0 Cambiar representaciones de s mismo, de los otros o de lo otro (desnaturalizando lo injusto o inequitativo o inconveniente para la salud).
        .BG03.p-4
          .p.mb-0 #[strong Nota]. Basada en Minsalud (2018, p.18).

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-7.order-lg-1.order-2(data-aos="fade-right")
        p.mb-0 En esencia, los elementos de la educacin en salud, desde una perspectiva ms persuasiva, apuntan hacia el cambio de comportamiento y a la promocin de estilos de vida saludables para la proteccin de la salud.  Esta perspectiva de la educacin que, como se ha dicho, se le conoce tambin como enfoque de conocimiento, persiste hoy en da y es el enfoque ms popular en las intervenciones en salud, bajo el supuesto que las personas cambiarn su conducta en la medida que conozcan el dao que genera un comportamiento determinado. Se parte de que las personas se comportan de manera racional y que con una informacin sobre riesgos cambiarn su conducta. Este enfoque, entonces, busca influir sobre las creencias, aptitudes y comportamientos de las personas, por ejemplo, mediante las campaas publicitarias en medios de comunicacin masiva, los cambios en los etiquetados y las medidas informativas y actitudinales.        
      .col-lg-5.col-10.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/tema5/img17.png' alt="Imagen decorativa")
    
    .row.justify-content-center.mb-5
      .col-lg-3.col-6.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema5/img18.png' alt="Imagen decorativa")
      .col-lg-9
        p Tanto el enfoque netamente informativo como el enfoque difusionista orientado a la conducta tienen grandes limitaciones: no toman en cuenta las dinmicas locales ni las mltiples dimensiones socioeconmicas que atraviesan al sujeto, y tampoco los medios o formas  de comunicacin locales, parten de un modelo biolgico individual de salud  enfermedad,  ponen las problemticas de salud en el comportamiento de los individuos, con poco o ningn inters por las cuestiones sociales, convirtiendo los comportamientos en el objetivo ltimo de las intervenciones, adoptando una mirada vertical y autoritaria, donde se debe acatar la directriz del personal de salud.  
        .BG02.p-4.mb-5(data-aos="fade-left")
          .row.justify-content-center.align-items-center
            .col-lg-2.col-4.px-4.mb-lg-0.mb-4(data-aos="fade-right")
              img(src='@/assets/curso/tema5/img19.svg' alt="Imagen decorativa")
            .col-lg-10(data-aos="fade-left")
              p.mb-0 Se basan en que el comportamiento cambiar a partir de generar conocimiento y modificar las actitudes de los individuos con informacin y tcnicas persuasivas dirigidas a los individuos, dndoles a entender la responsabilidad en su propia salud.  Parte de las crticas que hacen los autores es que estas tcnicas son manipuladoras, pues no toman en cuenta la libre voluntad de las personas, descartando el pleno uso de la inteligencia y las decisiones conscientes de los individuos.

    .row.justify-content-start.align-items-center.mb-4(data-aos="fade-right")
      .col-auto.px-0
        img(src='@/assets/curso/temas/img04.svg' style="max-width: 40px;")
      .col-auto.BGRS08
        .h4.mb-0 Educacin para la Salud vista como proceso transformador desde la autonoma de los sujetos
    
    .row.justify-content-center.mb-5
      .col-lg-5.col-10.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema5/img20.png' alt="Imagen decorativa")
      .col-lg-7
        p Este abordaje se transforma en los 80, la educacin para el desarrollo pone nfasis en la creacin de capacidad y construccin del sujeto: aqu se plantea la necesidad de reconocer la influencia que ejercen los factores sociales, econmicos, ambientales y las desigualdades socioeconmicas en relacin con la salud (y otras problemticas sociales) en el sujeto. Esta etapa tiene como fin:
        .row.justify-content-center
          .col-lg-6.px-0(data-aos="fade-right")
            .tarjeta-avatar-b.mb-5
              .tarjeta-avatar-b__img
                img(src='@/assets/curso/tema5/img21.svg' alt="Imagen decorativa")
              .tarjeta.BGR14.hg-07
                .p-4
                  .h4.mb-0 Disminuir las desigualdades sociales en los procesos salud - enfermedad, desde un enfoque crtico.  
          .col-lg-6.px-0(data-aos="fade-left")
            .tarjeta-avatar-b
              .tarjeta.BGR15.hg-07(style="margin-left: 0; padding-left: 0; padding-right: 10%")
                .p-4
                  .h4.mb-0 Desarrollar la conciencia e incentivar la participacin.
              .tarjeta-avatar-b__img(style="left: -10%")
                img(src='@/assets/curso/tema5/img22.svg' alt="Imagen decorativa")

    .BG02.mb-5(data-aos="flip-up")
      .row.justify-content-center.align-items-center
        .col-lg-1.col-2.pe-2.py-3.mb-lg-0.mb-4
          img(src='@/assets/curso/temas/img05.svg')
        .col.p-4 
          p.mb-0 La Educacin para la Salud es crtica, emancipadora, participativa, atravesada por tcnicas pedaggicas y discursivas de educacin popular, investigacin participativa, organizacin popular.

    .BGIMG01.mb-5(data-aos="flip-up")
      .row.justify-content-start.align-items-center
          .col-lg-7.order-lg-1.order-2
            .row.justify-content-start.align-items-center
              .col-2.ps-5.pe-4.py-4
                img(src='@/assets/curso/temas/img01.svg')
              .col
                .h5 La Educacin para la Salud, entonces, se adhiere al Paradigma Alternativo de la educacin, cuyo mximo exponente es Paulo Freire, donde la educacin se orienta al empoderamiento y la autodeterminacin de las comunidades para la definicin de su propio modelo de desarrollo, siendo decisores y participantes activos del proceso.
          .col-5.d-lg-none.d-flex
          .col-lg-3.col-6.px-lg-2.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema5/img01.png' alt="Imagen decorativa")
    
    p.mb-5 Estos paradigmas de la Educacin para la Salud se articulan a los tipos de educacin segn sus nfasis (Kapln, 2002); primero, educacin dirigida al conocimiento; segundo, educacin dirigida a los efectos; y tercero, educacin basada en el proceso, que implica que los modelos sean exgenos o endgenos al individuo, y que se vea al otro como objeto o como sujeto:

    .row.justify-content-center.mb-5
