                td(style="background-color: #F0F3FA;") Objeto.
                td(style="background-color: #F0F3FA;") Efectos (pone el acento en el cambio de comportamiento).
              tr
                td(style="background-color: #FFF;") Endgeno (parte del destinatario).
                td(style="background-color: #FFF;") Sujeto.
                td(style="background-color: #FFF;") Proceso (pone el acento en el cambio social, desde la reflexin y toma de consciencia del sujeto).
              tr
                td(style="background-color: #F0F3FA;" colspan="3") Estos modelos son tipos ideales; en la realidad, los modelos pueden estar mezclados, pero puede acentuarse en determinado modelo y nfasis, sin prescindir de los otros modelos o nfasis.

    p.mb-5 Los nfasis se pueden definir como:

    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema5/img23.png' alt="Imagen decorativa")
      .col-lg-8(data-aos="fade-left")
        TabsA.color-acento-contenido
          .tarjeta.BGR03.p-4(titulo="Contenidos")
            h4 Educacin que pone el nfasis en los contenidos
            p.mb-0 Es la educacin realizada por profesores a alumnos, tiene como fin transmitir conocimiento.  Este tipo de educacin es considerada como vertical, autoritaria, denominada por Paulo Freire como bancaria; lo que se busca es dar instrucciones o conocimientos a una persona o comunidad considerada a #[em priori] ignorante. Aqu, el educador es el instruido y el educando es el ignorante.
          .tarjeta.BGR03.p-4(titulo="Efectos")
            h4 Educacin que pone el nfasis en los efectos
            p.mb-0 Llamada tambin ingeniera del comportamiento, busca moldear la conducta de las personas con objetivos o conductas definidas de antemano. En este caso, el comunicador/educador se concibe como un arquitecto de la conducta humana. Es tambin llamada comunicacin manipuladora.
          .tarjeta.BGR03.p-4(titulo="Proceso")
            h4 Educacin que pone el nfasis en el proceso
            p.mb-0 Aqu, lo importante es la transformacin de la persona desde el dilogo y la toma de conciencia social.  Su inters principal est en el desarrollo de capacidad.  Se busca propiciar reflexiones que guen una toma de conciencia individual o colectiva.  El educador/comunicador es un facilitador del proceso.

