    separador
    #t_5_3.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 5.3 Fundamentos pedaggicos para la educacin de las RIAS

    p.mb-5 El Ministerio de Salud y Proteccin Social ha hecho avances y desarrollos significativos sobre el tipo de Educacin para la Salud que debe realizarse en el marco de la salud pblica, y define la Educacin para la Salud como: 

    .BG05.p-4.mb-5
      .row.justify-content-center.align-items-center
        .col-lg-2.col-4.px-4.mb-lg-0.mb-4(data-aos="fade-right")
          img(src='@/assets/curso/tema5/img24.svg' alt="Imagen decorativa")
        .col-lg-10(data-aos="fade-left")
          .h5.mb-5 El proceso pedaggico (dialgico e intencionado) de construccin de conocimiento y aprendizaje que, mediante el dilogo de saberes, pretende construir o fortalecer el potencial de las personas, familias, comunidades y organizaciones para promover el cuidado de la salud, gestionar el riesgo en salud y transformar positivamente los entornos en los que se desenvuelven sus vidas 
          .h5.text-end.mb-0 <i style="color: #4ADBD1; padding-right: 10px" class="fas fa-square"></i>(Resolucin 3280 de 2018).

    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema5/img25.png' alt="Imagen decorativa")
      .col-lg-8(data-aos="fade-left")
        .BG02.mb-5
          .row.justify-content-center
            .col-lg-1.col-2.pe-2.py-3.mb-lg-0.mb-4
              img(src='@/assets/curso/temas/img05.svg')
            .col.p-4 
              p.mb-0 En ese sentido, el proceso de Educacin para la Salud en el marco de las RIAS invita a reconocer la diversidad de actores y de voces, a construir horizontalmente sentidos y valores sobre la salud, el cuidado y el bienestar, reconociendo que los sujetos son el producto de su historia en un contexto particular, en un mundo en constante cambio. As, es de total relevancia reconocer la historia de los sujetos, los significados que le otorgan (ellos y su cultura) a determinados saberes, prcticas y relaciones que explican su salud, as como comprender la relacin poblacin - territorio, para reconocer cmo han llegado a ser lo que son y, a partir de ello, construir o fortalecer conjuntamente saberes, valores y prcticas que aporten a la promocin y mantenimiento de su salud. En el siguiente cuadro, se resume la postura pedaggica de la Educacin para la Salud en el marco de las RIAS:

    .row.justify-content-center.mb-5
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos="fade-right" alt="Muestra, Fundamentos pedaggicos que sustentan la EpS en el marco de las RIAS, nmero, fundamentos pedaggicos y sustentacin.")
          h5 Tabla 4
          span Fundamentos pedaggicos que sustentan la EpS en el marco de las RIAS
        .tabla-b.color-acento-contenido(data-aos="zoom-in")
          table
