              tr
                td(style="border: 2px solid #E8E8E8; background-color: #FFF;") 1
                td(style="border: 2px solid #E8E8E8; background-color: #FFF;") Enfoque de Educacin para la Salud
                td(style="border: 2px solid #E8E8E8; background-color: #FFF;") 1 De segunda generacin: EpS comportamental.<br>2 De tercera generacin: EpS participativa y emancipatoria.
            tbody
              tr
                td(style="border: 2px solid #E8E8E8; background-color: #F0F3FA;") 2
                td(style="border: 2px solid #E8E8E8; background-color: #F0F3FA;") Modelo pedaggico
                td(style="border: 2px solid #E8E8E8; background-color: #F0F3FA;") Interestructurante dialogante
              tr
