                td(style="border: 2px solid #E8E8E8; background-color: #F0F3FA;") Dilogo de saberes
              tr
                td(style="border: 2px solid #E8E8E8; background-color: #FFF;") 7
                td(style="border: 2px solid #E8E8E8; background-color: #FFF;") Tcnicas educativas
                td(style="border: 2px solid #E8E8E8; background-color: #FFF;") De educacin popular, constructivista, de desarrollo de pensamiento crtico, de aprendizaje significativo

    p.mb-5 La perspectiva pedaggica desde la cual se realizarn las acciones de EpS en el marco de las RIAS se basa en:

    .row.justify-content-start.align-items-center.mb-4(data-aos="fade-right")
      .col-auto.px-0
        img(src='@/assets/curso/temas/img04.svg' style="max-width: 40px;")
      .col-auto.BGRS08
        .h4.mb-0 Constructivismo social
      
    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema5/img26.png' alt="Imagen decorativa")
      .col-lg-8(data-aos="fade-left")
        p.mb-0 El constructivismo parte del hecho de que el conocimiento es una construccin del ser humano y no una copia de la realidad. El aprendizaje es esencialmente activo y, en la medida en que una persona aprende algo nuevo, lo incorpora a sus experiencias previas y a sus propias estructuras mentales.  En este contexto, el constructivismo social est basado en la premisa de que los nuevos conocimientos se forman a partir de los propios esquemas de la persona (producto de su realidad) y de su comparacin con los esquemas de los dems individuos que lo rodean. El paso de un estadio a otro sucede como producto de la interaccin social (dentro de un contexto cultural particular) e implica la reorganizacin de esquemas y estructuras mentales.  En este camino, las personas se apropian de los conocimientos que han sido construidos por la sociedad y adquieren y complejizan sus propios procesos psicolgicos superiores (lenguaje, razonamiento, etc.). As, el proceso interpersonal queda transformado en otro intrapersonal.

    .BGIMG01.mb-5(data-aos="flip-up")
      .row.justify-content-start.align-items-end
          .col-xxl-7.col-lg-8.order-lg-1.order-2
            .row.justify-content-start.align-items-center
              .col-2.ps-5.pe-4.py-4
                img(src='@/assets/curso/temas/img01.svg')
              .col.p-4
                .h5.mb-0 Vygotsky considera que el aprendizaje es uno de los mecanismos fundamentales de desarrollo y, por tanto, la enseanza debe adelantarse a este para jalonarlo.  Lpez (s.f.)  considera que el aprendizaje est determinado por el medio en el que se desenvuelve el sujeto y, desde este punto de vista, la interaccin social actividad colectiva- es el motor del desarrollo. Por lo anterior, hay que conocer:
          .col-5.d-lg-none.d-flex
          .col-lg-2.col-4.px-lg-2.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema5/img27.png' alt="Imagen decorativa")

    .row.justify-content-center.mb-4
      .col-lg-10.mb-4(data-aos="flip-up")
        .tarjeta-avatar-b(data-aos="fade-right")
          .tarjeta-avatar-b__img(style="min-width: 9%")
            img(src='@/assets/curso/tema5/img28.svg' alt="Imagen decorativa")
          .tarjeta.BGR04S(style="margin-left:-5%")
            .p-4
              .h5.mb-0 Cul es la zona de desarrollo prximo de cada sujeto, es decir, cul es la distancia entre el nivel real de desarrollo (lo que puede hacer solo) y el nivel de desarrollo potencial (lo que puede hacer mediante una gua o la cooperacin de otro).
      .col-lg-10(data-aos="flip-up")
        .tarjeta-avatar-b.mb-5(data-aos="fade-right")
          .tarjeta-avatar-b__img(style="min-width: 9%")
            img(src='@/assets/curso/tema5/img29.svg' alt="Imagen decorativa")
          .tarjeta.BGR04P(style="margin-left:-5%")
            .p-4
              .h5.mb-0 Cules son los mejores mediadores para desarrollar las capacidades (mediadores instrumentales: lenguaje, objetos, etc.; y mediadores sociales: padres, cuidadores, TH en salud, etc.).

    .BG05.p-4.mb-5
      .row.justify-content-center.align-items-center
        .col-lg-2.col-4.px-4.mb-lg-0.mb-4(data-aos="fade-right")
          img(src='@/assets/curso/tema5/img30.svg' alt="Imagen decorativa")
        .col-lg-10(data-aos="fade-left")
          .h5.mb-0 El conocimiento, entonces, no es un objeto que se transmite de uno a otro, sino una construccin que depende del medio social en el que est inmersa la persona, y que es adquirido mediante operaciones mentales cognitivas (contrastar, construir, comunicar, pensar), que se despliegan tanto por la actividad propia como por la participacin en escenarios de actividad cooperativa para el aprendizaje. Uno de los mtodos por excelencia para lograr el aprendizaje lo constituyen los proyectos.

    .row.justify-content-start.align-items-center.mb-4(data-aos="fade-right")
      .col-auto.px-0
        img(src='@/assets/curso/temas/img04.svg' style="max-width: 40px;")
      .col-auto.BGRS08
        .h4.mb-0 Aprendizaje significativo
    
    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema5/img31.png' alt="Imagen decorativa")
      .col-lg-8(data-aos="fade-left")
        p Basado en los planteamientos de Ausubel, el proceso pedaggico debe partir de conocer la estructura cognitiva previa de los sujetos, para, desde all, preparar la nueva informacin, que se conecte de forma sustancial (no arbitraria) con un aspecto existente y relevante de tal estructura conceptual. Esa nueva informacin afectar la estructura existente mediante su reordenamiento (subordinacin, supraordinacin, categorizacin, recategorizacin, inclusin de un nuevo concepto) (Rodrguez, 2004).   Todo lo anterior requiere de dos premisas: por una parte, la motivacin de la persona por aprender, y por otra, que quien organiza el proceso pedaggico averige previamente cul es esa estructura, qu tan estable es, y qu experiencias previas tiene el sujeto, con el fin de disear las estrategias didcticas que permitan que los nuevos aprendizajes se vinculen a las ideas previas de forma significativa, lo cual se traduce en mayor retencin de conocimientos.
        p.mb-0 Dentro de los requisitos para lograr aprendizajes significativos, se encuentran:

    .row.justify-content-center.mb-5
      .col-lg-4.col-md-8.mb-lg-0.mb-4(data-aos="fade-right")
        .BG10.hg-08
          .figure
            img(src='@/assets/curso/tema5/img32.jpg' alt="Imagen decorativa")
          .px-4.pb-4
            .row.justify-content-around.align-items-center.mb-2
              .col
                .h4.mb-0 Significatividad lgica del material
              .col-lg-2.col-3.img-01
                img(src='@/assets/curso/tema5/img09.svg' alt="Imagen decorativa")
            p.mb-0 El material debe tener una estructura interna que permita construir nuevos significados.
      .col-lg-4.col-md-8.mb-lg-0.mb-4(data-aos="fade-right")
        .BG11.hg-08
          .figure
            img(src='@/assets/curso/tema5/img33.jpg' alt="Imagen decorativa")
          .px-4.pb-4
            .row.justify-content-around.align-items-center.mb-2
              .col
                .h4.mb-0 Significatividad psicolgica del material
              .col-lg-2.col-3.img-01
                img(src='@/assets/curso/tema5/img09.svg' alt="Imagen decorativa")
            p.mb-0 La persona debe poder conectar el nuevo conocimiento con los saberes que ya estn en su estructura cognitiva, es decir, debe ser comprensible.
      .col-lg-4.col-md-8.mb-lg-0.mb-4(data-aos="fade-right")
        .BG10.hg-08
          .figure
            img(src='@/assets/curso/tema5/img34.jpg' alt="Imagen decorativa")
          .px-4.pb-4
            .row.justify-content-around.align-items-center.mb-2
              .col
                .h4.mb-0 Actitud orientada al aprendizaje por parte del sujeto
              .col-lg-2.col-3.img-01
                img(src='@/assets/curso/tema5/img09.svg' alt="Imagen decorativa")
            p.mb-0 La persona debe querer aprender, adems de poder hacerlo.

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-1.col-2.mb-lg-0.mb-4
        img.w-50(src='@/assets/curso/temas/img05.svg')
      .col
        p.mb-0 Es importante recordar que quien orienta el proceso pedaggico debe:
    
    .row.justify-content-center.mb-4
      .col-lg-10.mb-4(data-aos="flip-up")
        .tarjeta-avatar-b(data-aos="fade-right")
          .tarjeta-avatar-b__img(style="min-width: 9%")
            img(src='@/assets/curso/tema5/img35.svg' alt="Imagen decorativa")
          .tarjeta.BGR04S(style="margin-left:-5%")
            .p-4
              .h5.mb-0 Generar experiencias para que las personas puedan participar activamente en la resolucin de un problema concreto de la realidad.
      .col-lg-10(data-aos="flip-up")
        .tarjeta-avatar-b.mb-5(data-aos="fade-right")
          .tarjeta-avatar-b__img(style="min-width: 9%")
            img(src='@/assets/curso/tema5/img36.svg' alt="Imagen decorativa")
          .tarjeta.BGR04P(style="margin-left:-5%")
            .p-4
              .h5.mb-0 Identificar cmo cada participante del proceso educativo organiza mentalmente la informacin y cul es la relacin que tiene con su medio, para ayudarle a encontrar sentido a lo que aprende y desarrollar sus capacidades.

    .row.justify-content-start.align-items-center.mb-4(data-aos="fade-right")
      .col-auto.px-0
        img(src='@/assets/curso/temas/img04.svg' style="max-width: 40px;")
      .col-auto.BGRS08
        .h4.mb-0 Pedagoga crtica- educacin popular
    
    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema5/img37.png' alt="Imagen decorativa")
      .col-lg-8(data-aos="fade-left")
        p Se basa en la idea de que es posible crear una sociedad justa y equitativa, por tanto, se requiere empoderar a las personas, quienes, mediante una lectura crtica de la realidad y la problematizacin de las relaciones sociales (de los poderes que circulan en ella), sern capaces de develar intereses, conflictos, y por tanto, de actuar para mejorar la situacin que viven (Meja, 2016; Torres, 1993).
        .BG02.mb-5
          .row.justify-content-center
            .col-lg-1.col-2.pe-2.py-3.mb-lg-0.mb-4
              img(src='@/assets/curso/temas/img05.svg')
            .col.p-4 
              p.mb-0 La pretensin de la pedagoga crtica es crear nuevas formas de conocimiento que partan de: 1&#41; develar el carcter reproductor de injusticias sociales; 2&#41; generar un modo de leer la historia (desde la construccin de un proyecto de identidad de los sujetos); 3&#41; hacer que el conocimiento curricular responda al conocimiento cotidiano desde las historias de vida de los participantes (contextualizacin de los aprendizajes); y 4&#41; destacar la primaca de lo tico. Todo lo anterior se profundiza en el siguiente video:

    figure.mb-5(data-aos="zoom-in")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/CSrntNl6MVw" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)

    p Se considera que los que aprenden son personas activas, capaces de investigar, y que el educador deber generar escenarios pedaggicos basados en la pregunta, que les permitan comprender la realidad, crear y decir sus propias palabras.
    p.mb-5 En suma, se trata de formar para la accin y la transformacin, rescatando el papel del mediador (pedagogo) como aquel acompaante capaz de disear opciones, delinear caminos, proponer conflictos cognitivos, valorativos y orientar alternativas de solucin. Desarrollar un proceso de educacin desde la perspectiva crtica-educacin popular implica:

    .row.justify-content-center.mb-5
      .col-lg-8
        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Figura 7
          span Educacin desde la perspectiva crtica-educacin
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/tema5/img38.svg' alt="Muestra el reconocimiento de saberes previos: a partir de saberes previos significa no solo reconocer y valorar los saberes del otro, sino exige una responsabilidad tica. aprendemos desde o que sabemos, pero tambin contra a pesar de ellos.  Aprendizaje colectivo, dialogo de saberes y problematizacin de la realidad.")
          figcaption Nota. <a style="font-weight: 400">Tomada de Ministerio de Salud y Proteccin Social (2018).</a>

    .BG05.p-4.mb-5
      .row.justify-content-center.align-items-center
        .col-lg-2.col-4.px-4.mb-lg-0.mb-4(data-aos="fade-right")
          img(src='@/assets/curso/tema5/img39.svg' alt="Imagen decorativa")
        .col-lg-10(data-aos="fade-left")
          .h5.mb-0 Por lo anterior y en consonancia con los referentes pedaggicos definidos, los enfoques de Educacin para la Salud hacia la realizacin de las intervenciones definidas en las Rutas Integrales de Atencin en Salud (RIAS) son el comportamental y el crtico-participativo; con un modelo pedaggico interestructurante dialogante, donde se reconocen todas las dimensiones de la vida humana (cognitiva, socioafectiva, prxica) y todos los tipos de inteligencia.

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.order-lg-1.order-2(data-aos="fade-right")
        p Para el caso del presente documento, el modelo que orientar la EpS en el marco de las RIAS es el interestructurante dialogante, de forma que se aporte al desarrollo de sujetos crticos, con instrumentos para pensar, para valorar y para actuar de forma reflexiva, reconociendo el saber de los otros y participando en construcciones conjuntas de conocimiento.
        .BG02.mb-5
          .row.justify-content-center.align-items-center
            .col-lg-1.col-2.pe-2.py-3.mb-lg-0.mb-4
              img(src='@/assets/curso/temas/img05.svg')
            .col.p-4 
              p.mb-0 Para este modelo y postura en polticas nacionales de Educacin para la Salud en el marco de las RIAS, el dilogo de saberes es el fundamento que permite una EpS basada en los sujetos y orientada a la autonoma de estos.
      .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/tema5/img40.png' alt="Imagen decorativa")
    
    separador
    #t_5_4.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 5.4 Dilogo de saberes

    .BGIMG02.mb-5(data-aos="flip-up")
      .row.justify-content-end.align-items-end
        .col-lg-1.d-lg-none.d-flex
        .col-lg-3.col-6.mb-lg-0.pt-lg-0.pt-4.pe-lg-0.pe-5.mb-4
          img(src='@/assets/curso/tema5/img41.png' alt="Imagen decorativa")
        .col-lg-8.p-4
          .row.justify-content-start
            .col-2.ps-5.pe-4
              img(src='@/assets/curso/temas/img01.svg')
            .col
              p.mb-0 El dilogo de saberes se entiende como una interaccin entre diversos conocimientos, donde se reconoce que tanto los sectores populares como el talento humano formado en espacios acadmicos tiene saberes que no solo se diferencian por los campos de contenidos que cada uno de ellos tiene, sino porque han sido construidos desde lgicas culturales y prcticas histricas diferentes.

    p.mb-5 Lo que hay que reconocer en el dilogo con los participantes de los procesos de EpS es que existen diversas y vlidas lgicas culturales y, por tanto, lo que corresponde es comprender la lgica interna de los discursos sobre los cuales los sujetos basan sus actuaciones (para este caso, su actuar en relacin con la salud y la vida). Una vez comprendido el discurso del otro, el ejercicio pedaggico se centrar en ampliar el universo de sentido de los sujetos que participan del proceso educativo (Torres, 1993).

    .row.justify-content-center.align-items-center
      .col-lg-8.order-lg-1.order-2(data-aos="fade-right")
        .BG02.mb-5
          .row.justify-content-center
            .col-lg-1.col-2.pe-2.py-3.mb-lg-0.mb-4
              img(src='@/assets/curso/temas/img05.svg')
            .col.p-4 
              p.mb-0 Lo anterior implica estar dispuesto a escuchar de forma activa e incluyente, en un marco de negociacin cultural, generando acuerdos, hasta construir nuevos saberes-sentires (esto quiere decir que en un dilogo no solo circulan conocimientos, sino sentimientos, sueos, intereses, temores, e incluso prcticas). En el dilogo, habr que percibir los lenguajes corporales, las intencionalidades, las relaciones entre actores, los poderes que circulan entre los participantes. Esto es imperativo para ser cuidadoso en el planteamiento del saber cientfico, de tal forma que permita reconocer #[strong qu se puede decir y qu no en ese contexto, qu les duele o a qu le temen, qu saben y qu no saben, qu quieren saber], etc. Los vacos de saber son vlidos tanto en los sujetos que aprenden como en los que ensean y habr que hacerlos evidentes para poder construir un nuevo saber.
      .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/tema5/img42.png' alt="Imagen decorativa")
  
    p En suma, el dilogo de saberes implica la comprensin de la cultura (propia y de los otros), para generar acciones que incidan sobre las estructuras simblicas de los participantes, lo cual se ver reflejado en su vida cotidiana.

    separador
    #t_5_5.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 5.5 Orientaciones para el desarrollo de Educacin para la Salud

    p.mb-5 La educacin y comunicacin para la salud se desarrolla tanto para las RIAS de riesgo como para la RPMS, el siguiente esquema permite comprender de qu manera se complementan estos escenarios: 

    .row.justify-content-center.mb-5
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Figura 8
          span Educacin para la Salud
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/tema5/img43.svg' alt="Muestra Educacin y comunicacin para la salud, teniendo en cuenta la Ruta de promocin de Mantenimiento de la salud (RPMS), que se desarrolla en cuidar, pensar crticamente y agenciar que es transformar. RIAS de riesgo de evento, tiene gestin de riesgo, comprender, sentir, actuar.")
          figcaption Nota. <a style="font-weight: 400">Tomado de Ministerio de Salud y Proteccin Social (2018).</a>
    
    .BG02.mb-4(data-aos="flip-up")
      .row.justify-content-center.align-items-center
        .col-lg-1.col-2.pe-2.py-1.mb-lg-0.mb-4
          img(src='@/assets/curso/temas/img05.svg')
        .col.p-4
          p.mb-0 La educacin para la salud puede brindarse bajo la forma de educacin individual y grupal (como parte de las atenciones y procedimientos con cargo a la UPC, es decir, bajo la responsabilidad de la EpS o asegurador), as como educacin colectiva, es decir, intervencin colectiva (bajo responsabilidad de la entidad territorial de salud y la ESE como operador). En el siguiente recurso, se mencionan algunas orientaciones para tener en cuenta: 

    .row.justify-content-start.align-items-center.mb-4(data-aos="fade-right")
      .col-auto.px-0
        img(src='@/assets/curso/temas/img04.svg' style="max-width: 40px;")
      .col-auto.BGRS08
        .h4.mb-0 Educacin individual - grupal - colectiva
    
    p.mb-5 Las sesiones educativas deben ser implementadas por uno o varios profesionales, tcnicos y tecnlogos de las ciencias de la salud, salud ambiental y de las ciencias sociales y humanas, con el apoyo de auxiliares del equipo de salud con las competencias para desarrollar metodolgica, pedaggica y tcnicamente el proceso (ideal el apoyo de profesional en pedagoga, psicopedagoga o psicologa educativa). Para el caso de comunidades indgenas, podrn ser desarrolladas por el personal de salud propio de dichos pueblos.

    .BGM07.px-md-5.px-4.mb-5
      .row.justify-content-center.mb-5
        .col-lg-4.col-md-8.mb-lg-0.mb-4(data-aos="fade-right")
          .BG10.hg-09.mb-4
            .figure
              img(src='@/assets/curso/tema5/img44.jpg' alt="Imagen decorativa")
            .px-4.pb-4
              .row.justify-content-around.align-items-center.mb-2
                .col
                  .h4.mb-0 Individual
                .col-lg-2.col-3.img-01
                  img(src='@/assets/curso/tema5/img09.svg' alt="Imagen decorativa")
              p.mb-0 Dirigida a un sujeto en un momento de curso de vida o a una familia.
          .BGR03A.hg-11.p-4
            p.mb-0 A cargo de EAPB - asegurador con la red de prestacin de servicios.
        .col-lg-4.col-md-8.mb-lg-0.mb-4(data-aos="flip-up")
          .BG11.hg-09
            .figure
              img(src='@/assets/curso/tema5/img45.jpg' alt="Imagen decorativa")
            .px-4.pb-4
              .row.justify-content-around.align-items-center.mb-2
                .col
                  .h4.mb-0 Grupal
                .col-lg-2.col-3.img-01
                  img(src='@/assets/curso/tema5/img09.svg' alt="Imagen decorativa")
              p Dirigida a personas que comparten determinado momento de curso de vida.
              p.mb-0 O a grupos de personas o familias que comparten alguna condicin o situacin.
        .col-lg-4.col-md-8.mb-lg-0.mb-4(data-aos="fade-left")
          .BG10.hg-09.mb-4
            .figure
              img(src='@/assets/curso/tema5/img46.jpg' alt="Imagen decorativa")
            .px-4.pb-4
              .row.justify-content-around.align-items-center.mb-2
                .col
                  .h4.mb-0 Colectivo
                .col-lg-2.col-3.img-01
                  img(src='@/assets/curso/tema5/img09.svg' alt="Imagen decorativa")
              p.mb-0 Dirigido a colectivos que comparten una condicin o situacin, personas que viven o se desarrollan en determinado entorno (algunos comparten ms el mismo momento de curso de vida). 
          .BGR03A.hg-11.p-4
            p.mb-0 A cargo de la DTS con la ESE  en el marco del plan de salud de intervenciones colectivas.
      .row.justify-content-start.align-items-center.mb-4(data-aos="fade-right")
        .col-auto.px-0
          img(src='@/assets/curso/temas/img04.svg' style="max-width: 40px;")
        .col-auto.BGRS08
          .h4.mb-0 Zoom - Educacin individual - grupal
      
      p.mb-5 Las capacidades y los contenidos de los ciclos de educacin para la salud pueden ser ajustados de acuerdo con:
      
      .row.justify-content-center.align-items-center
        .col-lg-9.order-lg-1.order-2(data-aos="fade-right")
          ul.lista-ul--color.lista-ul--separador
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle(style="color: #6F88DA;")
              | La identificacin de capacidades a ser fortalecidas con las personas o familias.
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle(style="color: #6F88DA;")
              | Las necesidades de salud identificadas.
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle(style="color: #6F88DA;")
              | Las inquietudes de los grupos con los que se trabajar. 
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle(style="color: #6F88DA;")
              | Las orientaciones pedaggicas, metodolgicas y didcticas contenidas en el documento de marco conceptual y metodolgico de Educacin (y comunicacin) para la Salud en el marco de las Rutas Integrales de Atencin en Salud - RIAS.
        .col-lg-3.col-6.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
          img(src='@/assets/curso/tema5/img47.png' alt="Imagen decorativa")

    .h4.mb-5 Cmo?

    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema5/img48.png' alt="Imagen decorativa")
      .col-lg-8(data-aos="fade-left")
        TabsA.color-acento-contenido
          .tarjeta.BGR03.p-4(titulo="Individual")
            p.mb-0 Considerando condiciones propias, estableciendo una tutora con plan de actividades o plan de acompaamiento e incorporando tcnicas de consejera.
          .tarjeta.BGR03.p-4(titulo="Grupal")
            p.mb-0  Considerando la caracterizacin poblacional y las caractersticas del momento del curso de vida y a travs de la definicin de ciclos de sesiones, incorporando talleres, estudio de casos, seminarios, entre otros.

    .h4.mb-5 Sesiones
    
    .row.justify-content-center.mb-5
      .col-lg-6.mb-lg-0.mb-4(data-aos="fade-right")
        .BGR03A.hg-10.p-4
          .h4 Individual
          ul.lista-ul--color.lista-ul--separador
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle(style="color: #6F88DA;")
              | De acuerdo con el plan de acompaamiento establecido.
            li(style="margin-bottom: 2px")
              i.fas.fa-check-circle(style="color: #6F88DA;")
              | Sesiones de 20 a 30 minutos (establecer formas de seguimiento y acompaamiento complementario virtual o telefnico).
